.miq-user-avatar {
    height: 32px;
    width: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    overflow: hidden;
    background-color: var(--blue-50);
    border: 2px solid white;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &-group {
        display: flex;
        direction: rtl;

        .miq-user-avatar {
            margin: -6px;

            &.count {
                margin-left: 8px;
            }
        }
    }
}

#DocumentUpdateView {
    .miq-view-title {
        // padding: utils.$coco;
        // padding: 12px;

        input {
            font-weight: 600;
            font-size: 18px;
            line-height: 22px;
            border: 1px solid transparent;
        }
    }
}

.document-list-view {
    .document-list-link {
        color: var(--gray-900);
        display: block;
        text-decoration: none;
        padding: var(--sp-1);
        margin-bottom: var(--sp-0);

        background-color: var(--gray-100);
        border-radius: var(--border-radius);

        &:hover {
            background-color: var(--blue-50);
        }

        .document-title {
            margin-bottom: var(--sp-0);
        }
        .document-meta {
            display: flex;
            align-items: center;
            font-size: var(--fs-sm);

            .count {
                margin-right: 4px;
            }
        }
    }
}

